import React, { useEffect } from "react";
import { isBrowser } from "react-device-detect";

import "../home/home.css";
import "./assets/style.css";

import { Item } from "./assets/Item";
import { List } from "./assets/List";
import Footer from "../../components/footer/Footer";

import { Route, Switch } from "react-router-dom";
import { NoMatchPage } from "../../App";

import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import LocoScroll from "../../hooks/useLocoScroll";
// import { HoverCursor } from "../../components/CustomCursor/CursorHoverSeiitngs";
import { ScrollCardSkew } from "./assets/ScrollCardSkew";

import hImg from "../../components/navigation/images/image4.svg"


gsap.registerPlugin(ScrollTrigger);


function Store({ match }) {
    let { id } = match.params;
    const imageHasLoaded = true;
    return (
        <>
            <List selectedId={id} />
            {id && imageHasLoaded && <Item id={id} key="item" />}
        </>
    );
}

export const LoadAnimB = time => {
    const headerBl = document.querySelector('.header-bl');
    const headerImg = document.querySelector('.header-img');
    const containerBl = document.querySelector('.container-bl');

    if (headerBl || headerImg || containerBl) {
        // if (containerBl) {
        setTimeout(() => {
            gsap.to(headerImg, 2, { scale: 1, opacity: 1, ease: "Power4.easeOut" });
            gsap.fromTo(containerBl, 2, { opacity: 0, y: "5%" }, { opacity: 1, y: "0%", ease: "Power4.easeOut", delay: .5 });
        }, time);
        const welcome = gsap.timeline({
            scrollTrigger: {
                trigger: headerImg,
                scroller: ".sth-scrl",
                scrub: true,
                start: "top top",
                end: "+=400%",
                pin: true,
                pinSpacing: false
            }
        });
        if (isBrowser) {
            welcome.to(headerImg, { x: "-30%" });
        } else {
            welcome.to(headerImg, { x: "-30%" });
        }

        ScrollTrigger.refresh();
    }
};

function Works() {

    LocoScroll();

    useEffect(() => {
        const preload = document.querySelector('.preloader');

        if (!preload) {
            LoadAnimB(200);
        };

        ScrollCardSkew();

    }, [])

    // useEffect(() => HoverCursor(), [])

    return (
        <div className="sth-scrl">
            <section className="header-bl">
                {/* <div className="container"><h2>Works</h2></div> */}
                <div className="header-img">
                    <img src={hImg} alt="img" />
                </div>
            </section>
            <div className="container container-bl">
                <Switch>
                    <Route exact path="/works/" component={Store} />
                    {/* <Route exact path={["/saa/blog/id/:id", "/saa/blog/"]} component={Store} /> */}
                    <Route component={NoMatchPage} />
                </Switch>
            </div>
            <Footer />
        </div>
    )
}

export default Works;