import React, { useEffect, useState } from "react";
import "./home.css";

import { BrowserView, isBrowser, isMobile } from "react-device-detect";

import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import LocoScroll from "../../hooks/useLocoScroll";
import { HoverCursor } from "../../components/CustomCursor/CursorHoverSeiitngs";

import { PagePagination } from "./PagePagination"
import Footer from "../../components/footer/Footer";
import Quotes from "./QuotesRandom";
import Circles from "./circles/indx";
import Whoami from "./whoami/indx";

import { useDataJson, styleDataJson, blogItems } from "../../hooks/useDataJson";

import { Link } from "react-router-dom";

import { ScrollBlock } from "../../hooks/ScrollBlock";



gsap.registerPlugin(ScrollTrigger);


export const loadAnimH = time => {

    const colorLines = document.querySelector('.color-lines');

    if (colorLines) {
        colorLines.classList.remove('act-h');
        setTimeout(() => {
            gsap.to('.quotes h2', 2, { left: 0, opacity: 1, ease: "Power4.easeOut", delay: .4 });
            gsap.to('.quotes p', 2, { left: 0, opacity: 1, ease: "Power4.easeOut", delay: 1 });
            gsap.to('.tr--1', 2.5, { scale: 1, opacity: 1, ease: "Power4.easeOut" });
            if (colorLines) { setTimeout(() => colorLines.classList.add('act-h'), 3000) };
        }, time);
    }
};

function Home() {

    const [itemsData, isLoaded, error] = useDataJson(blogItems.url);
    const [contLoaded, setcontLoaded] = useState(false);

    /////////////////////////   Page scroll animation

    const LScroll = LocoScroll()

    ScrollBlock(3000);

    useEffect(() => {

        HoverCursor();

        const preload = document.querySelector('.preloader');
        const pageNav = document.querySelector('#wel--co--me');

        if (!preload) {
            loadAnimH(400);
        }


        ///////////////////////////////////////////////

        if (contLoaded) {

            if (isMobile) {
                document.querySelector('#sectionPin').style.cssText = `height:auto`;
                document.querySelector('.tr--1').classList.add('mob-v');

            }

            if (isBrowser) {
                const welcome = gsap.timeline({
                    scrollTrigger: {
                        trigger: "#home",
                        scroller: ".sth-scrl",
                        scrub: true,
                        pin: true,
                        pinSpacing: true,
                        // markers: true,
                        start: "top top",
                        end: document.querySelector('#home').offsetWidth / 0.9,
                        ease: "none"
                    }
                });
                welcome
                    .fromTo('.quotes h2', 6, { x: "0" }, { x: "-133vw" }, '-=6')
                    .fromTo('.quotes p', 6, { x: "0" }, { x: "-100vw" }, '-=6')
                    .to('.st-anim', 6, { x: "-8vw" }, '-=6')
                    .to('.st-anim2', 6, { x: "-6vw" }, '-=6')
                    .to('.st-anim3', 6, { x: "-30vw" }, '-=6')
                    .to('.st-anim6', 6, { x: "4vw" }, '-=6')
                    .fromTo('.st-anim4', 6, { x: "7vw" }, { x: "-5vw" }, '-=6')
                    .fromTo('.st-anim5', 6, { x: "12vw" }, { x: "-6vw" }, '-=6')
                    .fromTo('.tr--1-sqr', 6, { x: "32vw" }, { x: "0" }, '-=6')
                    .fromTo('.tr--1-plc', 6, { x: "-32vw" }, { x: "0" }, '-=6')
                    .fromTo('.pag-pag-p', 6, { x: "-10vw" }, { x: "0" }, '-=6')
                    .fromTo('.tr--1', 6, { x: "0" }, { x: '-100vw' }, '-=6')
                    .to('.scroll-down', 1, { opacity: 0, delay: 1 }, '-=7');


                const fdImgHor = document.querySelectorAll('.f-dekstop .img-hor-cont');

                fdImgHor.forEach(tex => {
                    tex.style.cssText = `transform: translateY(30%)`;
                    gsap.to(tex, {
                        scrollTrigger: {
                            trigger: tex,
                            scroller: ".sth-scrl",
                            start: "top bottom",
                            // markers: true,
                            scrub: true,
                            end: "+=70%",
                        }, transform: 'translateY(0)'
                    });
                })
            }
            gsap.to(".wel-h2", {
                scrollTrigger: {
                    trigger: "#wel--co--me",
                    scroller: ".sth-scrl",
                    scrub: true,
                    start: "top bottom",
                    end: "top top"
                },
                y: '30%', ease: "none"
            }, '-=6');

            if (isBrowser) {
                const circlesCont = document.querySelector(".circles-cont");
                circlesCont.style.transform = "translateY(calc(80px + 10vw))";

                gsap.to(circlesCont, {
                    scrollTrigger: {
                        trigger: "#wel--co--me",
                        scroller: ".sth-scrl",
                        scrub: true,
                        start: "top bottom",
                        end: "+=90%",
                    }, y: 0
                });
            }

            gsap.from(".color-lines", {
                scrollTrigger: {
                    trigger: ".color-lines",
                    scroller: ".sth-scrl",
                    scrub: true,
                    start: "top bottom",
                    end: "+=320%",
                    toggleClass: 'act-h'
                },
            })

            if (pageNav) {
                gsap.from(pageNav, {
                    scrollTrigger: {
                        trigger: pageNav,
                        scroller: ".sth-scrl",
                        // scrub: true,
                        start: "top bottom"
                    }, onComplete: function () { pageNav.classList.add('act'); }
                });
            };

            if (isBrowser) {
                let pinWrap = document.querySelector(".pin-wrap");
                gsap.to(pinWrap, {
                    scrollTrigger: {
                        scroller: ".sth-scrl",
                        scrub: true,
                        trigger: "#sectionPin",
                        pin: true,
                        invalidateOnRefresh: true,
                        // anticipatePin: 1,
                        start: "top top",
                        end: () => "+=" + pinWrap.offsetWidth / 1.5
                    },
                    x: () => -(pinWrap.scrollWidth - document.documentElement.clientWidth) + "px",
                    ease: "none",
                });

                let proxy = { skew: 0 },
                    skewSetter = gsap.quickSetter(".skewElem", "skewX", "deg"),
                    clamp = gsap.utils.clamp(-20, 20);

                ScrollTrigger.create({
                    scroller: ".sth-scrl",
                    trigger: "#sectionPin",
                    start: "top top",
                    end: () => "+=" + pinWrap.offsetWidth / 1.5,
                    onUpdate: self => {
                        let skew = clamp(self.getVelocity() / -300);
                        if (Math.abs(skew) > Math.abs(proxy.skew)) {
                            proxy.skew = skew;
                            gsap.to(proxy, { skew: 0, duration: 0.3, ease: "power3", overwrite: true, onUpdate: () => skewSetter(proxy.skew) });
                        }
                    }
                });
                gsap.set([".skewElem", "#home"], { force3D: true }); // - fix 
            };



            if (isMobile) {
                const fMbileImgHor = document.querySelectorAll('.img-hor-box .img-hor');
                if (fMbileImgHor) {
                    fMbileImgHor.forEach(img => {
                        img.style.cssText = `transform: translateY(40%)`;
                        gsap.to(img, .7, {
                            scrollTrigger: {
                                trigger: img,
                                scroller: ".sth-scrl",
                                start: "top bottom",
                                end: "bottom bottom"
                            }, transform: 'translateY(0)'
                        });
                    })
                }
            }
        };

        ScrollTrigger.refresh();

    }, [contLoaded]);

    return (
        <>
            {/* <ScrollBlock /> */}
            <div className="sth-scrl" ref={LScroll}>
                <BrowserView>
                    <PagePagination />
                </BrowserView>
                <div id="whome" style={{ position: 'absolute', top: 0, left: 0, height: '100vh' }}></div>
                <section id="home" className="panel blue">
                    <div className="wel-h2">
                        <Quotes />
                        <div className="color-lines">
                            <div className="tr--1">
                                <svg viewBox="0 0 2036 1179.6"><g><rect x="303.1" y="594.4" transform="matrix(0.7071 -0.7071 0.7071 0.7071 -556.301 631.8013)" className="st0" width="362.8" height="786" /><circle className="st1" cx="762.4" cy="1265.2" r="181.4" /><path className="st2" d="M948.7,1137l-556-555l-57.4-0.4l552.8,552.8c34.3,33,55.7,79.4,55.7,130.8c0,90.4-66.2,165.4-152.7,179.1c55.6,9,114.7-7.9,157.6-50.7C1019.6,1322.8,1019.5,1207.9,948.7,1137z" /></g><path className="st1" d="M498-33c-165.9,165.9-165.9,435,0,600.9l1.4,1.4l153,153l0,0l0.4,0.4c77,77,208.4,82.8,290.5,11.3c43.4-37.9,160.5-159.6,160.5-159.6L498-33L498-33z" /><g><path className="st2" d="M616.5,85.8c10.3-12.5,21.1-24.5,32.4-35.8c110.8-110.8,251.2-93.2,371.8,0.7c0,0,63.4,0,62.7-0.7l85.2-85.2c-167-167.1-437.8-167.1-604.9-0.1c-11.5,11.5-22.2,23.5-32.1,35.9L616.5,85.8z" /><path className="st4" d="M496.5-35.7c-167.1,167.1-167.1,437.9,0,605l85.3-85.2c-120-120-120-314.5,0-434.5s314.5-120,434.5,0l85.2-85.2C934.4-202.7,663.6-202.7,496.5-35.7z" /><ellipse className="st0" cx="538.1" cy="526.5" rx="60.4" ry="60.4" /></g><g><path className="st2" d="M1460.4,269.9c-89.3-89.3-208.2-130.9-325-124.7c98.1,7.6,194.1,49,269.2,124c166.9,166.9,167.1,437.1,0.7,604.3l0,0l55.1,1.4C1627.5,707.8,1627.5,437,1460.4,269.9z" /><path className="st3" d="M799.6,269.2c167.1-167.1,437.9-167.1,605,0s167.1,437.9,0,605L799.6,269.2z" /></g></svg>
                                <svg className="st-anim6" viewBox="0 0 2036 1179.6"><g><path className="st4" d="M1116.2,1009.3l1,1c7.1,7.1,7.1,18.5,0,25.5s-18.5,7.1-25.5,0L963,907.2L899.2,971l127.7,127.7c42.3,42.3,110.9,42.3,153.2,0c42.3-42.3,42.3-110.9,0-153.2L1116.2,1009.3z" /><path className="st0" d="M1205.6,919.9l1,1c7.1,7.1,7.1,18.5,0,25.5s-18.5,7.1-25.5,0l-128.7-128.7l-63.8,63.8l127.7,127.7c42.3,42.3,110.9,42.3,153.2,0c42.3-42.3,42.3-110.9,0-153.2L1205.6,919.9z" /><circle className="st3" cx="931.1" cy="939.1" r="45.1" /><ellipse className="st1" cx="1020.4" cy="849.8" rx="45.1" ry="45.1" /><circle className="st1" cx="1237.5" cy="888" r="45.1" /></g></svg>
                                <svg className="st-anim" viewBox="0 0 2036 1179.6"><path className="st3" d="M38.1,964.7l302.8-302.8c167.2,167.2,167.2,438.3,0,605.5L38.1,964.7z" /></svg>
                                <svg className="st-anim2" viewBox="0 0 2036 1179.6"><circle className="st0 cir-anim6" cx="1103.4" cy="574.4" r="212.4" /></svg>
                                <svg className="st-anim3" viewBox="0 0 2036 1179.6"><g><path className="st4" d="M297.5,1066.7c-87.9,87.8-87.9,230.3,0,318.1l51.3-51.3c-59.5-59.5-59.5-156,0-215.5s156-59.5,215.5,0l51.3-51.3C527.8,978.9,385.4,978.9,297.5,1066.7z" /><linearGradient id="SVGID_1_" gradientUnits="userSpaceOnUse" x1="5651.7256" y1="3154.8491" x2="5651.7256" y2="3227.4497" gradientTransform="matrix(0.7071 -0.7071 -0.7071 -0.7071 -1149.8734 7345.1968)"><stop offset="0" style={{ stopColor: "#F8F6EA" }} /><stop offset="0.1631" style={{ stopColor: "#EEC9B1" }} /><stop offset="0.3852" style={{ stopColor: "#E29877" }} /><stop offset="0.5881" style={{ stopColor: "#D97451" }} /><stop offset="0.7647" style={{ stopColor: "#D45A38" }} /><stop offset="0.9079" style={{ stopColor: "#D1472C" }} /><stop offset="1" style={{ stopColor: "#c54a37" }} /><stop offset="1" style={{ stopColor: "#c54a37" }} /></linearGradient><circle className="st5" cx="590" cy="1092.4" r="36.3" /><linearGradient id="SVGID_2_" gradientUnits="userSpaceOnUse" x1="5651.7964" y1="3031.4578" x2="5651.7964" y2="3104.0586" gradientTransform="matrix(0.7071 -0.7071 -0.7071 -0.7071 -1149.8734 7345.1968)"><stop offset="0" style={{ stopColor: "#F8F6EA" }} /><stop offset="0.1631" style={{ stopColor: "#EEC9B1" }} /><stop offset="0.3852" style={{ stopColor: "#E29877" }} /><stop offset="0.5881" style={{ stopColor: "#D97451" }} /><stop offset="0.7647" style={{ stopColor: "#D45A38" }} /><stop offset="0.9079" style={{ stopColor: "#D1472C" }} /><stop offset="1" style={{ stopColor: "#c54a37" }} /><stop offset="1" style={{ stopColor: "#c54a37" }} /></linearGradient><circle className="st6 cir-anim3" cx="677.3" cy="1179.6" r="36.3" /></g></svg>
                                <svg className="st-anim4" viewBox="0 0 2036 1179.6"><g><path className="st4" d="M1404.5,158.7c-87.8,87.9-87.8,230.3,0,318.1l51.3-51.3c-59.5-59.5-59.5-156,0-215.5s156-59.5,215.5,0l51.3-51.3C1634.8,70.9,1492.4,70.9,1404.5,158.7z" /><linearGradient id="SVGID_3_" gradientUnits="userSpaceOnUse" x1="7076.5596" y1="3014.1335" x2="7076.5596" y2="3086.7341" gradientTransform="matrix(0.7071 -0.7071 -0.7071 -0.7071 -1149.8734 7345.1968)"><stop offset="0" style={{ stopColor: "#F8F6EA" }} /><stop offset="0.1631" style={{ stopColor: "#EEC9B1" }} /><stop offset="0.3852" style={{ stopColor: "#E29877" }} /><stop offset="0.5881" style={{ stopColor: "#D97451" }} /><stop offset="0.7647" style={{ stopColor: "#D45A38" }} /><stop offset="0.9079" style={{ stopColor: "#D1472C" }} /><stop offset="1" style={{ stopColor: "#c54a37" }} /><stop offset="1" style={{ stopColor: "#c54a37" }} /></linearGradient><circle className="st7" cx="1697" cy="184.4" r="36.3" /><linearGradient id="SVGID_4_" gradientUnits="userSpaceOnUse" x1="7076.6304" y1="2890.7422" x2="7076.6304" y2="2963.3428" gradientTransform="matrix(0.7071 -0.7071 -0.7071 -0.7071 -1149.8734 7345.1968)"><stop offset="0" style={{ stopColor: "#F8F6EA" }} /><stop offset="0.1631" style={{ stopColor: "#EEC9B1" }} /><stop offset="0.3852" style={{ stopColor: "#E29877" }} /><stop offset="0.5881" style={{ stopColor: "#D97451" }} /><stop offset="0.7647" style={{ stopColor: "#D45A38" }} /><stop offset="0.9079" style={{ stopColor: "#D1472C" }} /><stop offset="1" style={{ stopColor: "#c54a37" }} /><stop offset="1" style={{ stopColor: "#c54a37" }} /></linearGradient><circle className="st8 cir-anim" cx="1784.3" cy="271.6" r="36.3" /><linearGradient id="SVGID_5_" gradientUnits="userSpaceOnUse" x1="7076.6304" y1="2767.4216" x2="7076.6304" y2="2840.0222" gradientTransform="matrix(0.7071 -0.7071 -0.7071 -0.7071 -1149.8734 7345.1968)"><stop offset="0" style={{ stopColor: "#F8F6EA" }} /><stop offset="0.1631" style={{ stopColor: "#EEC9B1" }} /><stop offset="0.3852" style={{ stopColor: "#E29877" }} /><stop offset="0.5881" style={{ stopColor: "#D97451" }} /><stop offset="0.7647" style={{ stopColor: "#D45A38" }} /><stop offset="0.9079" style={{ stopColor: "#D1472C" }} /><stop offset="1" style={{ stopColor: "#c54a37" }} /><stop offset="1" style={{ stopColor: "#c54a37" }} /></linearGradient><circle className="st9 cir-anim2" cx="1871.5" cy="358.8" r="36.3" /></g></svg>
                                <svg className="st-anim5" viewBox="0 0 2036 1050"><g><path className="st2" d="M1689.1,877.2c-11,0-21.8,0.8-32.3,2.4c105.7,12.2,187.7,102,187.7,211c0,106.3-78.1,194.4-180.1,210c8.1,0.9,16.4,1.4,24.7,1.4c117.3,0,212.4-95.1,212.4-212.4S1806.4,877.2,1689.1,877.2z" /><path className="st2" d="M1681.7,1405.4v-24.1c77.9,0,151.1-30.3,206.1-85.4s85.4-128.2,85.4-206.1s-30.3-151.1-85.4-206.1s-128.2-85.4-206.1-85.4v-24.1c42.6,0,83.9,8.3,122.9,24.8c37.6,15.9,71.3,38.6,100.3,67.6s51.7,62.7,67.6,100.3c16.5,38.9,24.8,80.2,24.8,122.8s-8.3,83.9-24.8,122.8c-15.9,37.6-38.7,71.3-67.6,100.3s-62.7,51.7-100.3,67.6C1765.6,1397.1,1724.3,1405.4,1681.7,1405.4z" /><path className="st4" d="M1632.1,878.2c-117.3,0-212.4,95.1-212.4,212.4s95.1,212.4,212.4,212.4s212.4-95.1,212.4-212.4S1749.4,878.2,1632.1,878.2z M1632.1,1196.9c-58.7,0-106.3-47.6-106.3-106.3s47.6-106.3,106.3-106.3s106.3,47.6,106.3,106.3S1690.8,1196.9,1632.1,1196.9z" /></g></svg>
                                <svg viewBox="0 0 2036 1179.6"><ellipse className="st0 cir-anim4" cx="538.1" cy="526.5" rx="60.4" ry="60.4" /></svg>
                                <svg className="st-anim6" viewBox="0 0 2036 1179.6"><circle className="st3 cir-anim5" cx="931.1" cy="939.1" r="45.1" /></svg>
                                {(isBrowser) ?
                                    <>
                                        <div className="tr--1-sqr"></div>
                                        <div className="tr--1-plc"><svg viewBox="0 0 276.14 277.39"><path className="st0" d="M258.61,261.95V15.74C123.79,15.74,14.5,125.03,14.5,259.85c0,0.7,0.02,1.4,0.03,2.11H258.61z" /></svg></div>
                                        <div className="page-pagination">
                                            <p className="pag-pag-p">page pagination</p>
                                            <a href="#wel--co--me" data-scroll data-scroll-to data-scroll-target="#wel--co--me" data-crsr-zoomout><h2>About</h2></a>
                                            <a href="#sectionPin" data-scroll data-scroll-to data-scroll-target="#sectionPin" data-crsr-zoomout><h2>works</h2></a>
                                            <a href="#sectionPin" data-scroll data-scroll-to data-scroll-target="#sectionPin" data-crsr-zoomout><h2>clients</h2></a>
                                            <a href="#done" data-scroll data-scroll-to data-scroll-target="#done" data-crsr-zoomout><h2>contact</h2></a>
                                        </div>
                                    </> : null}
                            </div>
                        </div>
                        <div className="scroll-down">
                            <svg viewBox="0 0 90 90"><path d="M34.7,67.5c-11.3,0-20.5-9.2-20.5-20.5V22c0-11.3,9.2-20.5,20.5-20.5S55.3,10.7,55.3,22v25C55.3,58.3,46.1,67.5,34.7,67.5z"></path><circle className="sr-d" cx="34.3" cy="21.6" r="7"></circle></svg>
                        </div>
                    </div>
                </section>
                <section id="wel--co--me">
                    <Circles />
                </section>
                {/* <section>
                    <Whoami />
                </section> */}
                <section id="sectionPin">
                    {(error) ? <div className="l--data" ref={() => setcontLoaded(true)} style={styleDataJson.loadError}><h4>Error: {error.message}</h4></div> : (!isLoaded) ? <div style={styleDataJson.loadError}><h4>Loading...</h4></div> :
                        <>
                            {
                                (isBrowser) ?
                                    <div className="pin-wrap" >
                                        <div className="f-dekstop">
                                            {/* <div style={{ width: '60vw' }}><h2><span>My most recent works as a</span> Motion Designer, Designer, Web designer and Web developer</h2></div> */}
                                            {itemsData.slice(-5).reverse().map(item => {
                                                const { id, title, titleimage, titlettemple, category } = item;
                                                return (
                                                    <div className="img-hor-cont" key={id} style={{ position: 'relative' }} ref={() => setcontLoaded(true)}>
                                                        <div className="img-hor skewElem" data-crsr-eye>
                                                            <Link to={`/works/p-n/${id}`}>
                                                                <div className="img-cont" style={{ backgroundImage: `url(${titleimage})` }} />
                                                            </Link>
                                                            <div className="category">{category}</div>
                                                            <div className="skewElemPrlx">
                                                                <h3>{title}</h3>
                                                                <p>{titlettemple}</p>
                                                                <hr style={{ margin: "1vw 0" }} />
                                                            </div>
                                                        </div>
                                                        <div className="img-cont-border" />
                                                    </div>
                                                )
                                            }
                                            )}
                                            <Link to="/works" data-crsr-more>
                                                <div className="img-hor" style={{ margin: '0 10vw 0 10vw', width: '27vw', justifyContent: 'flex-start' }}>
                                                    <h2 style={{ textAlign: 'left' }}>more</h2>
                                                </div>
                                            </Link>
                                        </div>
                                    </div>
                                    :
                                    <div className="f-mobile">
                                        <div className="container">
                                            <div style={{ marginBottom: "calc(50px + 2vw)" }}><h2><span>My most recent works as a</span> Motion Designer, Designer, Web designer and Web developer</h2></div>
                                            <div className="img-hor-box" >
                                                {itemsData.slice(-5).reverse().map(item => {
                                                    const { id, title, titleimage } = item;
                                                    return (
                                                        <div key={id} ref={() => setcontLoaded(true)} className="img-hor">
                                                            <Link to={`/works/p-n/${id}`}>
                                                                <div className="img-cont" style={{ backgroundImage: `url(${titleimage})` }} />
                                                            </Link>
                                                            <h3 className="skewElemPrlx">{title}</h3>
                                                        </div>
                                                    )
                                                }
                                                )}
                                            </div>
                                            <div style={{ display: 'flex', justifyContent: 'center' }}>
                                                <Link to={`/works/`}>
                                                    <button>more works</button>
                                                </Link>
                                            </div>
                                        </div>
                                    </div>
                            }
                        </>
                    }
                </section>
                {/* <section className="panel gray" id="done">
                    WELL DONE!
                </section> */}
                {(isBrowser) ? <div className="go-tt-page"><a href="#whome" data-scroll data-scroll-to data-scroll-target="#whome" data-crsr-zoomout>up</a></div> : null}
                <Footer />
            </div >
        </>
    )
}

export default Home;