import React, { useEffect } from "react";
import "../home/home.css";

import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import LocoScroll from "../../hooks/useLocoScroll";

import Footer from "../../components/footer/Footer";
import { ScrollBlock } from "../../hooks/ScrollBlock";

import imgAbout from "../../components/navigation/images/image3.svg"

gsap.registerPlugin(ScrollTrigger);

function About() {

    LocoScroll();

    ScrollBlock(1500);

    return (
        <div className="sth-scrl">
            <div className="container" style={{marginTop:'20vh'}}>
                <h3 style={{textAlign:'center'}}>Coming Soon</h3>
                <div style={{ position: 'relative', width: '100%', height: 'auto', margin:'6vh 0'}}>
                    <img style={{positon:'absolute', width: '100%', height: '100%', objectFit:'cover'}} src={imgAbout} alt="about" />
                </div>
            </div>
            <Footer />
        </div>
    )
}

export default About;