import React, { useEffect, useRef } from "react";
import "./style.css";
import { gsap } from "gsap";

import { Link, useRouteMatch } from "react-router-dom";
import { isBrowser } from "react-device-detect";

import { ContactNav } from "./Contact";
import { Toggle } from "../Toggle"

import img1 from "./images/image1.svg";
import img2 from "./images/image2.svg";
import img3 from "./images/image3.svg";

function OldSchoolMenuLink({ label, to, activeOnlyWhenExact }) {
    let match = useRouteMatch({
        path: to,
        exact: activeOnlyWhenExact
    });
    return (
        <Link className={match ? "active" : ""} to={to} data-crsr-zoomout >{label}</Link>
    );
}

const homeSpan = () => <h2><span>h</span><span>o</span><span>m</span><span>e</span></h2>;
const aboutSpan = () => <h2><span>a</span><span>b</span><span>o</span><span>u</span><span>t</span></h2>;
const worksSpan = () => <h2><span>w</span><span>o</span><span>r</span><span>k</span><span>s</span></h2>;
// const worksSpan = () => <svg viewBox="0 0 426.7 108.6"><g><path className="st2" d="M42.7,72.3v0.4c0,3.1-2.5,5.5-5.5,5.5c-3.1,0-5.5-2.5-5.5-5.5l-0.1-56.1H3.7v55.6c0,18.4,14.9,33.3,33.3,33.3s33.3-14.9,33.3-33.3L42.7,72.3L42.7,72.3z"/><path className="st1" d="M81.6,72.3v0.4c0,3.1-2.5,5.5-5.5,5.5c-3.1,0-5.5-2.5-5.5-5.5l-0.1-56.1H42.7l0.1,55.6c0,18.4,14.9,33.3,33.3,33.3s33.3-14.9,33.3-33.3L81.6,72.3L81.6,72.3z"/><circle className="st1" cx="17.6" cy="16.7" r="13.9"/><circle className="st0" cx="56.6" cy="16.7" r="13.9"/><circle className="st0" cx="95.6" cy="72.3" r="13.9"/><circle className="st3" cx="95.6" cy="16.7" r="13.9"/></g><g><path className="st0" d="M190.1,54.4c0,13-10.6,23.5-23.5,23.5c-13,0-23.5-10.4-23.5-23.5c0-13,10.4-23.5,23.5-23.5c0.1,0,0.1,0,0.2,0V3c-0.1,0-0.1,0-0.2,0c-28.4,0-51.5,22.9-51.5,51.5S138,106,166.6,106c28.4,0,51.3-22.9,51.5-51.3h-28V54.4z"/><ellipse className="st4" cx="166.8" cy="16.9" rx="14" ry="14"/><g><circle className="st2" cx="204.3" cy="54.4" r="14"/></g></g><g><rect x="223.1" y="16.4" className="st1" width="27.2" height="88.2"/><path className="st0" d="M257.1,61.8c16.3,0,29.3-13.2,29.3-29.3S273.3,3,257.1,3V61.8z"/><circle className="st3" cx="236.7" cy="16.6" r="13.8"/><polygon className="st4" points="286.6,104.7 257.1,104.7 257.1,67.7"/></g><g><polygon className="st4" points="330,66.1 330,3 359.4,3"/><rect x="295.6" y="3" className="st1" width="27.2" height="101.8"/><path className="st2" d="M329.5,104.9h29.8c0-16.5-13.4-29.8-29.8-29.8V104.9z"/></g><g><path className="st0" d="M409.8,11.6c-11.5-11.5-30.2-11.5-41.6,0c-11.5,11.5-11.5,30.2,0,41.6L409.8,11.6z"/><path className="st4" d="M414.7,54.6c11.5,11.5,11.5,30.2,0,41.6c-11.5,11.5-30.2,11.5-41.6,0L414.7,54.6z"/><rect x="374.3" y="42.7" transform="matrix(0.7071 -0.7071 0.7071 0.7071 75.9991 292.2421)" className="st3" width="32.9" height="23.4"/></g></svg>;
