const Navigation = () => {

    const nmBtn = useRef(null);
    const navMenu = useRef(null);
    let navMenuC = useRef(null);
    const navMenuCa = useRef(null);
    const varyaLoh = useRef(null);

    useEffect(() => {
        const navMenuA = document.querySelectorAll('.nav-menu-l ul li a');
        const navMenuALi = document.querySelectorAll('.nav-menu-l ul li');
        const navMenuAB = document.querySelector('.nav-menu-l ul');
        const navMenuABL = document.querySelectorAll('.nav-menu label');
        const navMenuCADiv = document.querySelectorAll('.nav-menu-c-a .nav-menu-c-a-d');
        const logo = document.querySelector('.logo');
        const cRight = document.querySelector('.c-right');
        const bfix = document.body;
        varyaLoh.current.remove();

        nmBtn.current.addEventListener('click', () => {
            navMenuAB.style.pointerEvents = "none";
            nmBtn.current.style.pointerEvents = "none";
            nmBtn.current.classList.toggle('act');

            if (nmBtn.current.className === 'nm-btn act') {
                navMenu.current.className = 'nav-menu act';
                navMenuCADiv.forEach(l => gsap.to(l, { opacity: 0, x: '50%' }));
                navMenuABL.forEach(l => l.style.cssText = `opacity:0`);
                navMenuABL.forEach(l => l.style.cssText = `opacity:0`);
                gsap.to(navMenuAB, { x: 0 });
                setTimeout(() => logo.classList.add('act'), 600);
                setTimeout(() => {
                    const navAnimOpen = time => {
                        gsap.to(navMenuALi[0], time, { opacity: 1, x: '0%', delay: .6, ease: "Power3.easeOut" });
                        gsap.to(navMenuALi[1], time, { opacity: 1, x: '0%', delay: .5, ease: "Power3.easeOut" });
                        gsap.to(navMenuALi[2], time, { opacity: 1, x: '0%', delay: .4, ease: "Power3.easeOut" });
                        ////////////////
                        gsap.to(navMenuABL[0], time, { opacity: 1, delay: 1, });
                        gsap.to(navMenuABL[1], time, { opacity: 1, delay: .8, });
                        ////////////////
                        gsap.to(navMenuCADiv[0], time, { opacity: 1, x: '0%', delay: .3, ease: "Power3.easeOut" });
                        gsap.to(navMenuCADiv[1], time, { opacity: 1, x: '0%', delay: .2, ease: "Power3.easeOut" });
                        gsap.to(navMenuCADiv[2], time, { opacity: 1, x: '0%', delay: .1, ease: "Power3.easeOut" });
                        // gsap.to(navMenuCADiv[3], 1, { opacity: 1, x: '0%', delay: 0, ease: "Power3.easeOut" });
                    };
                    if (window.innerWidth < 960) {
                        navAnimOpen(0.7)
                    };
                    if (window.innerWidth > 960) {
                        navAnimOpen(1)
                    };
                }, 700);
                setTimeout(() => {
                    nmBtn.current.style.pointerEvents = "painted";
                    navMenuAB.style.pointerEvents = "painted";
                }, 1200);
            } else {
                gsap.to(navMenuAB, 1, { delay: .1, x: '35%' });
                navMenu.current.className = 'nav-menu act-left';
                nmBtn.current.style.pointerEvents = "none";
                logo.classList.remove('act');
                setTimeout(() => {
                    navMenu.current.className = 'nav-menu no-act';
                    nmBtn.current.style.pointerEvents = "painted";
                    navMenu.current.className = 'nav-menu';
                    ////////////////
                    navMenuABL.forEach(l => l.style.cssText = `opacity:0`);
                    navMenuCADiv.forEach(l => gsap.to(l, { opacity: 0, x: '50%' }));
                    navMenuALi.forEach(l => gsap.to(l, { opacity: 0, x: '35%' }));
                    // console.log('nav-menu');
                }, 1200);
            }
            bfix.classList.toggle('act');
        })

        const closeMenu = () => {
            // gsap.to('.nav-menu-l', .2, { opacity: 0, x: '-35%'});
            setTimeout(() => {
                bfix.classList.remove('act');
                navMenu.current.className = "nav-menu no-act";
                nmBtn.current.className = "nm-btn no-act";
                ////////////////
                navMenuABL.forEach(l => l.style.cssText = `opacity:0`);
                navMenuALi.forEach(l => gsap.to(l, { opacity: 0, x: '35%' }));
                navMenuCADiv.forEach(l => gsap.to(l, { opacity: 0, x: '50%' }));
            }, 900);
            setTimeout(() => {
                navMenu.current.className = "nav-menu";
                nmBtn.current.className = "nm-btn";
            }, 950);
            setTimeout(() => {
                logo.classList.remove('act');
                nmBtn.current.className = "nm-btn";
            }, 450);
        }

        navMenuA.forEach(i => {
            i.addEventListener('click', closeMenu);
            i.addEventListener('click', () => {
                navMenuAB.style.pointerEvents = "none";
                setTimeout(() => navMenuAB.style.pointerEvents = "painted", 700)
            });
        });

        logo.addEventListener('click', function () {
            closeMenu();
            logo.classList.add('act');
        });

        if (isBrowser) {
            navMenuA.forEach((event, image) => {
                event.addEventListener('mouseenter', () => {
                    navMediaArr()
                    gsap.fromTo(navMenuC.current, { scale: 1.4 }, { opacity: 1, scale: 1.1, duration: .8, ease: "Power3.easeOut" });
                    navMenuCa.current.style.opacity = 0;
                    gsap.to(cRight, .2, { opacity: 0 });
                })
                event.addEventListener('mouseleave', () => {
                    navMediaArr()
                    gsap.fromTo(navMenuC.current, { opacity: 1, scale: 1.1 }, { opacity: 0, scale: 1, duration: .8, ease: "Power3.easeOut" });
                    navMenuCa.current.style.opacity = 1;
                    gsap.to(cRight, .2, { opacity: 1 });
                })
                event.addEventListener('mouseup', () => {
                    gsap.fromTo(navMenuC.current, { scale: 1.4 }, { opacity: 1, scale: 1.1, ease: "Power3.easeOut" });
                })

                function navMediaArr() {
                    navMenuC.current.style.backgroundImage =
                        (image === 0) ? `url("${img1}")` :
                            (image === 1) ? `url("${img3}")` :
                                (image === 2) ? `url("${img2}")` : null;
                };

            });
        }

    }, [])

    return (
        <>
            <div className="navigation">
                <OldSchoolMenuLink activeOnlyWhenExact={true} to="/" label={homeLogo()} />
                <div className="nm-btn" ref={nmBtn} data-crsr-zoomout>
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 437 437"><path d="M404.1,134H32.9c-15.4,0-27.8-12.5-27.8-27.8v0c0-15.4,12.5-27.8,27.8-27.8h371.1c15.4,0,27.8,12.5,27.8,27.8v0C431.9,121.6,419.4,134,404.1,134z"></path><path d="M404.1,358.7H32.9c-15.4,0-27.8-12.5-27.8-27.8v0c0-15.4,12.5-27.8,27.8-27.8h371.1c15.4,0,27.8,12.5,27.8,27.8v0C431.9,346.2,419.4,358.7,404.1,358.7z"></path></svg>
                </div>
                <Toggle />
                <div className="nav-menu" ref={navMenu}>
                    <div className="nav-menu-l">
                        <ul>
                            <label>menu</label>
                            <li><OldSchoolMenuLink activeOnlyWhenExact={true} to="/" label={homeSpan()} /></li>
                            <li><OldSchoolMenuLink activeOnlyWhenExact={true} to="/about" label={aboutSpan()} /></li>
                            <li><OldSchoolMenuLink activeOnlyWhenExact={true} to="/works" label={worksSpan()} /></li>
                            {/* <li><OldSchoolMenuLink activeOnlyWhenExact={true} to="/contact" label={contactSpan()} /></li> */}
                        </ul>
                    </div>
                    <div className="nav-menu-c">
                        <div className="nav-menu-c-b" ref={navMenuC}>
                            <div ref={varyaLoh}>
                                <img src={img1} alt="home" />
                                <img src={img3} alt="about" />
                                <img src={img2} alt="works" />
                                {/* <img src={img4} alt="contact" /> */}
                            </div>
                        </div>
                        <div className="nav-menu-c-a" >
                            <div ref={navMenuCa}>
                                <label>contact</label>
                                <ContactNav />
                            </div>
                        </div>
                    </div>
                    <div className="c-right"><p>2023  Ivanwebstudio.<br />All rights reserved.</p></div>
                </div>
            </div>
        </>
    )
}

export default Navigation;