import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { isBrowser } from "react-device-detect";

gsap.registerPlugin(ScrollTrigger);

export const ScrollCardSkew = () => {

  if (isBrowser) {
    const skewElem = document.querySelector('.skewElem');

    if (skewElem) {
      let proxy = { skew: 0 };
      let skewSetter = gsap.quickSetter(".skewElem", "skewY", "deg");
      let clamp = gsap.utils.clamp(-20, 20);

      ScrollTrigger.create({
        scroller: '.sth-scrl',
        trigger: '.sth-scrl',
        onUpdate: (self) => {
          let skew = clamp(self.getVelocity() / -500);
          if (Math.abs(skew) > Math.abs(proxy.skew)) {
            proxy.skew = skew;
            gsap.to(proxy, { skew: 0, duration: 1.5, ease: "power3", overwrite: true, onUpdate: () => skewSetter(proxy.skew) });
          }
        }
      });
      gsap.set(".skewElem", { force3D: true }); // - fix 
      // ScrollTrigger.refresh();
    }
  }
}