import React, { useEffect } from "react";

export const BlogNavigation = ({ setStoriesToShow, storiesPerPage, loadMoreBtn, copyItems }) => {

    // sessionStorage.setItem("category", nameOfcategory);
    // console.log(sessionStorage.getItem("category"))

    const allCategory = () => {
        loadMoreBtn.current.style.display = "flex";
        setStoriesToShow(copyItems.slice(0, storiesPerPage));
    };

    const filterCategory = nameOfcategory => {
        loadMoreBtn.current.style.display = "flex";
        let filterCatagory = 0;
        filterCatagory = copyItems.filter(element => { // if (element.category === nameOfcategory) { return element.category === nameOfcategory; }
            if (element.category.length - 1) {
                loadMoreBtn.current.style.display = "none"
            } else {
                loadMoreBtn.current.style.display = "flex"
            }
            return element.category === nameOfcategory;
        })
        setStoriesToShow(filterCatagory);
    };

    useEffect(() => {
        const blNav = document.querySelectorAll('.btn');
        blNav.forEach(btn => {
            btn.addEventListener('click', function () {
                toggleClass(blNav, this);
            });
        });

        function toggleClass(blNav, btnActivate) {
            blNav.forEach(btn => btn.classList.remove('active'));
            btnActivate.classList.add('active');
        }
    })

    // const pointOfInterest = () => {
    //   loadMoreBtn.current.style.display = "flex";
    //   const copyItems = itemsData.concat();
    //   let copyDataSort;
    //   if (storiesToShowCopy) {
    //     copyDataSort = copyItems.sort((a, b) => ((a.pointOfInterest) < (b.pointOfInterest)) ? 1 : -1)
    //   } else {
    //     copyDataSort = copyItems
    //   }
    //   setStoriesToShow(copyDataSort.slice(0, storiesPerPage));
    //   setStoriesToShowCopy(!storiesToShowCopy);
    // };


    return (
        <div className="blog-navigation">
            <div onClick={() => allCategory()} data-crsr-zoomout className="btn active">all</div>
            <div onClick={() => filterCategory("Design")} data-crsr-zoomout className="btn">Design</div>
            <div onClick={() => filterCategory("Motion Design")} data-crsr-zoomout className="btn">Motion Design</div>
            <div onClick={() => filterCategory("Web Design and developed")} data-crsr-zoomout className="btn">web developed</div>
        </div>
    )
}
