import React, { useEffect, useRef } from "react";
import { loadAnimH } from "../pages/home/Home";
import { LoadAnimB } from "../pages/blog/Blog";

export default function Preloader() {

  const preload = useRef();
  const preloadPersent = useRef();

  useEffect(() => {
    const img = document.images;
    const img_tc = img.length;
    let img_lc = 0;

    for (let i = 0; i < img_tc; i++) {
      let img_cl = new Image();
      img_cl.onload = img_ld;
      img_cl.onerror = img_ld;
      img_cl.src = img[i].src;
    };

    // if (img_tc === 0) {
    //   preloadPersent.current.innerText = ' ';
    //   console.log('loaded')
    // };

    function img_ld() {
      img_lc++;
      preloadPersent.current.innerText = (((100 / img_tc) * img_lc) << 0) + "%";
      if (img_lc >= img_tc) {
        preaAnim()
      }
    }

    function preaAnim() {
      preloadPersent.current.innerText = ' ';
      setTimeout(() => preload.current.classList.add('act'), 500);
      setTimeout(() => {
        if (loadAnimH) {
          loadAnimH();
        }
        if (LoadAnimB) {
          LoadAnimB(0);
        }
      }, 1900);
      setTimeout(() => preload.current.remove(), 4000);
    }
  }, []);

  return (
    <div className="preloader" ref={preload}>
      <div className="preloader-a"></div>
      <h4 className="preloader-i" ref={preloadPersent}>Hello, please wait...</h4>
    </div>
  )

};
