import React, { useEffect } from "react";
import "../circles/style.css";

import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

const Circles = () => {
    useEffect(() => {
        const circles = document.querySelector(".circles");
        const colorsCircle = ["#519ac2", "#c54a37", "#ebddbd", "#f3ae3e", ""];
        const circlesArray = Array(40).map((e, i) => i + 1);
        const circleArrayCopy = circlesArray.concat();

        gsap.to(circles, {
            scrollTrigger: {
                trigger: ".circles-cont",
                // markers: true,
                start: "top bottom",
                end: +"100%",
                onToggle: self => { circles.classList.toggle("active") },
                onUpdate() {
                    // circles.classList.toggle("active")
                }
            }
        });

        function circlesFnctn(i) {
            const circlesSpan = document.createElement("span");
            circlesSpan.className = "circles-s";
            circlesSpan.innerHTML = i;
            circles.appendChild(circlesSpan);

            function circlesAnim() {
                const circlesSize = (Math.random() * 0.5) + 0.4;
                const bgColor = colorsCircle[Math.floor(Math.random() * colorsCircle.length)];
                circlesSpan.style.backgroundColor = bgColor;
                circlesSpan.style.transform = `scale(${circlesSize})`;
            } circlesAnim();

            (circles.className === "circles active") ? setInterval(circlesAnim, 3000) : clearInterval(circlesAnim);

        } circleArrayCopy.fill("").map(i => circlesFnctn(i));

    }, [])

    return (
        <div className="circles-cont">
            <div className="circles active"></div>
        </div>
    )
}

export default Circles;